"use client";
import Link from "next/link";
import { Arvo } from "next/font/google";
import React, { useState, useContext, useEffect } from "react";
import { useRouter } from "next/navigation";
import { auth } from "@/firebase/firebase";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  signInWithPopup,
  GoogleAuthProvider,
} from "firebase/auth";
import { AuthContext } from "@/context/AuthContext";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const arvo = Arvo({
  weight: '400', // or '700' for bold
  subsets: ['latin'],
});

export default function LoginPage() {
  // ... [keep all the existing state and functions the same] ...

  return (
    <div className="min-h-screen flex flex-col md:flex-row">
      {/* Left Side - Login Form */}
      <div className="w-full md:w-1/2 flex flex-col items-center justify-center bg-gray-50 p-4 h-screen">
        <div className="max-w-md w-full">
          {/* Logo Section */}
          <div className="flex items-center gap-3 mb-4 justify-center">
            <img 
              src="logo.ico" 
              alt="Shopkeeper Logo" 
              className="w-12 h-12"
            />
            <span className={`text-4xl ${arvo.className} text-gray-700`}>
              SHOPKEEPER
            </span>
          </div>

          <h1 className={`text-2xl mb-6 text-center ${arvo.className} text-gray-600`}>
            Welcome to Shopkeeper
          </h1>

          {/* ... [keep the form inputs the same] ... */}

          <div className="w-full">
            <button
              onClick={handleGoogleSignIn}
              className="w-full bg-white text-gray-700 py-2 rounded border border-gray-300 hover:bg-gray-100 transition flex items-center justify-center gap-2"
            >
              <img 
                src="/google-icon.png" 
                alt="Google Logo" 
                className="w-5 h-5"
              />
              <span>Sign in with Google</span>
            </button>
          </div>
        </div>
      </div>

      {/* Right Side - Image */}
      <div className="w-full md:w-1/2 h-screen hidden md:block relative">
        <div
          className="absolute inset-0 bg-cover bg-center"
          style={{ backgroundImage: "url('/image.jpg')" }}
        >
          <div className="absolute inset-0 bg-black opacity-10"></div>
        </div>
      </div>
    </div>
  );
}